@import url(https://fonts.googleapis.com/css?family=Libre+Baskerville);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  font-family: 'Libre Baskerville', serif;
  position: absolute;
  width: 100%;
  height: 100%;
  box-sizing: border-box; }

.About {
  position: absolute;
  box-sizing: border-box;
  display: table;
  width: 100%;
  height: 100%;
  text-align: center;
  padding: 10px;
  background-color: #333;
  color: #EFEFEF;
  text-shadow: 0 2px 10px #121212; }
  .About a {
    color: inherit; }
  .About .hoverable {
    text-shadow: 0 2px 10px #121212;
    -webkit-transition: text-shadow .5s ease;
    transition: text-shadow .5s ease; }
  .About .hoverable:hover {
    text-shadow: 0 2px 30px black;
    -webkit-transition: text-shadow .3s ease;
    transition: text-shadow .3s ease; }
  .About .clickable {
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    -webkit-transition: color .5s ease, text-shadow .5s ease;
    transition: color .5s ease, text-shadow .5s ease; }
  .About .clickable:hover {
    color: #58A8D5;
    text-shadow: 0 0 30px #58A8D5;
    -webkit-transition: color .3s ease, text-shadow .3s ease;
    transition: color .3s ease, text-shadow .3s ease; }
  .About .About-container {
    display: table-cell;
    vertical-align: top; }
    .About .About-container .info-container {
      margin-right: auto;
      margin-left: auto;
      margin-top: 8em; }
      .About .About-container .info-container .info {
        padding: 0 30px;
        font-size: 1em; }
        .About .About-container .info-container .info div {
          margin: 20px 50px; }
        .About .About-container .info-container .info .name {
          font-size: 4em; }
          .About .About-container .info-container .info .name .second-name {
            color: #58A8D5; }
        .About .About-container .info-container .info .desc {
          font-size: 1em;
          color: #bcbcbc;
          margin-bottom: 50px; }
        .About .About-container .info-container .info .links {
          font-size: 1.8em; }
          .About .About-container .info-container .info .links a {
            margin-left: 10px;
            margin-right: 10px; }

.IconizedLink {
  box-sizing: border-box;
  text-shadow: none; }
  .IconizedLink a > span {
    color: #EFEFEF;
    background: #3a708d; }
  .IconizedLink a:hover .icon {
    -webkit-transition: background-color .3s ease, color .3s ease, -webkit-transform .3s ease;
    transition: background-color .3s ease, color .3s ease, -webkit-transform .3s ease;
    transition: transform .3s ease, background-color .3s ease, color .3s ease;
    transition: transform .3s ease, background-color .3s ease, color .3s ease, -webkit-transform .3s ease;
    -webkit-transform: scale(1.2);
            transform: scale(1.2); }

.icon, .link {
  display: inline-block;
  font-size: 16px;
  line-height: 16px;
  vertical-align: middle; }

.icon {
  padding: 12px;
  border-radius: 50%;
  font-size: 24px;
  -webkit-transition: background-color .5s ease, color .5s ease, -webkit-transform .5s ease;
  transition: background-color .5s ease, color .5s ease, -webkit-transform .5s ease;
  transition: transform .5s ease, background-color .5s ease, color .5s ease;
  transition: transform .5s ease, background-color .5s ease, color .5s ease, -webkit-transform .5s ease; }

.link {
  padding: 8px;
  padding-left: 15px;
  border-radius: 0 10px 10px 0;
  margin-left: -10px; }
  @media screen and (max-width: 680px) {
    .link {
      display: none; } }

